body {
  background-color: #1e1e1e;
  color: white;
}
header {
  text-align: center;
  background-color: black;
  padding: 50px 0 150px;
  font-size: 2rem;
}
header i {
  color: #5e60ce;
  transform: rotate(-45deg);
}
header span:first-of-type {
  color: #4ea8de;
  font-weight: 900;
}
header span:last-of-type {
  color: #1e6f9f;
  font-weight: 900;
}

main {
  max-width: 600px;
  width: calc(100%-4rem);
  margin: 0 auto;
}

footer {
  text-align: center;
  padding: 2rem;
  color: lightgrey;
}

.inputArea {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  transform: translateY(-50%);
}
.inputArea > input,
.inputArea > button {
  border: none;
  outline: none;
}
.inputArea > input {
  border-radius: 1rem;
  padding: 1rem 2rem;
  background-color: rgb(227, 227, 227);
  flex: 1;
}
.inputArea > button {
  border-radius: 0.7rem;
  padding: 0.5rem 1rem;
  background-color: #4ea8de;
  color: white;
  cursor: pointer;
}

.count {
  padding: 1rem;
  border-bottom: 1px dotted rgba(255, 255, 255, 0.3);
  text-align: right;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 0.5rem;
}
.count span {
  background-color: aliceblue;
  padding: 0.2rem 0.5rem;
  color: #5e60ce;
  border-radius: 0.5rem;
}
.listCon {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding-top: 2rem;
}
.listCon > li {
  border: 1px solid lightgray;
  padding: 1rem;
  border-radius: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}
.listCon > li:hover {
  background-color: darkgrey;
}
.listCon > li > i {
  cursor: pointer;
  padding: 0.2rem 0.5rem;
}
.listCon > li > i:hover {
  color: #5e60ce;
}

.modal {
  position: fixed;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(10px);
  top: 0;
  left: 0;
}
.modal ._inner {
  width: 50%;
  max-width: 600px;
  margin: 200px auto 0;
  background-color: white;
  color: black;
  border-radius: 1rem;
  padding: 1rem;
  position: relative;
}
.modal ._inner button {
  position: absolute;
  top: -50px;
  left: 50%;
  transform: translateX(-50%);
}
